<template>
  <div class="virtual">
    <h4 style="margin-left:90px">虚拟长列表-渲染可视区</h4>
    <div class="virtual-content">
      <virtualList
        :viewHeight="viewHeight"
        :listData="listData"
        :itemSize="itemSize">
      </virtualList>
    </div>
  </div>
</template>

<script>
import virtualList from "@/components/global/virtualList"
export default {
  name:"Virtual",
  components:{virtualList},
  data(){
    return {
        viewHeight:300,
        listData:[],
        itemSize:60
    }
  },
  mounted(){
    for(let i=0;i<1000;i++){
      this.listData.push({value:i})
    }
  }
}
</script>
<style lang="less" scoped>
/* 组件大小取决于父组件大小 */
.virtual{
  padding: 16px;
  .virtual-content{
    box-sizing: border-box;
    width: 360px;
    height: 300px;
    border: 1px dashed #aaa;
  }
}
</style>



